  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

.hero__arrow {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -100%);
  z-index: 10;
}

.hero__triangles {
  position: absolute;
  top: 101%;
  left: 0;
  transform: translateY(-100%);
}

.hero__menu {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 100%;
