  margin-left: 3%;
}

.header__menu-burger {
  margin-top: 3%;
  margin-right: 3%;
  z-index: 100;
}

.menu-burger {
  display: block;
  width: 36px;
  height: 36px;
  position: relative;
}

.menu-burger.active .menu-burger__icon {
  background-color: transparent;
}

.menu-burger.active .menu-burger__icon:after,
.menu-burger.active .menu-burger__icon:before {
  top: 50%;
  left: 50%;
  transform-origin: top left;
  transition: transform .5s;
}

.menu-burger.active .menu-burger__icon:before {
  transform: rotate(-45deg) translate(-50%, -50%);
}

.menu-burger.active .menu-burger__icon:after {
  transform: rotate(45deg) translate(-50%, -50%);
}

.menu-burger__icon {
  height: 5px;
  width: 100%;
  background-color: #fff;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.menu-burger__icon:before {
  content: '';
  display: block;
  width: 36px;
  height: 5px;
  position: absolute;
  top: -300%;
  transition: top .5s;
  background-color: #fff;
  border-radius: 2px;
}

.menu-burger__icon:after {
  content: '';
  display: block;
  width: 36px;
  height: 5px;
  position: absolute;
  top: 300%;
  transition: top .5s;
  background-color: #fff;
  border-radius: 2px;
}

