  align-items: flex-end;
  text-align: right;
}

.paper-list__item:not(:last-of-type) {
  margin-bottom: 25px;
}

.paper-name {
  color: #455a64;
  width: fit-content;
  transition: border-right .3s;
  border-right: 3px solid transparent;
}

.paper-name__link {
  display: block;
  width: 100%;
  height: 100%;
  padding-right: 20px;
}

.paper-name:hover {
  color: #0ab19e;
  border-right: 3px solid #0ab19e;
  transition: border-right .5s;
}

.papers {
  padding-right: 110px;
}

.papers__item {
  padding-bottom: 70px;
}

.papers__item:not(:last-of-type) {
  border-bottom: 5px solid #f1f1f2;
}

.papers__item:not(:first-of-type) {
  padding-top: 70px;
}

.paper__title {
  font-family: 'Bernier';
  font-size: 40px;
  color: #455a64;
  margin-bottom: 30px;
}

.paper__date {
  color: #0ab19e;
  margin-bottom: 30px;
}

.main {
  height: 100vh;
  min-height: 620px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.main__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.main__auth {
  z-index: 20;
  align-self: flex-end;
  margin: 20px;
}

.main__welcome {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 380px;
  min-height: 360px;
  perspective: 1000px;
}

.main__credits {
  z-index: 20;
  margin-bottom: 3%;
}

.welcome {
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 4px 4px 0 0;
  height: 100%;
  position: relative;
  z-index: 2;
  /* for firefox 31 */
}

.welcome__buttons {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
}

.welcome__socials {
  margin-top: 25px;
}

.welcome__info {
  display: flex;
  flex-direction: column;
  padding: 35px 60px;
}

.welcome__avatar {
  border-radius: 50%;
  max-width: 40%;
  max-width: 150px;
}

.welcome__text {
  display: flex;
  flex-direction: column;
  color: #455a64;
}

.welcome__name {
  font-size: 28px;
}

.login {
  height: 100%;
  width: 380px;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 4px 4px 0 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  color: #455a64;
  position: relative;
  transform: rotateY(180deg);
  transform-origin: 50% 200px;
}

.login__buttons {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
}

.login__info {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 25px;
  height: 100%;
}

.flipper {
  width: 100%;
  min-height: 360px;
  transition: 0.6s;
  transform-style: preserve-3d;
  position: relative;
}

.flipper.back {
  transform: rotateY(180deg);
}

.flipper.front {
  transform: rotateY(0deg);
}

.welcome,
.login {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.welcome {
  z-index: 200;
}

.login {
  transform: rotateY(180deg);
}

.inputs {
  width: 100%;
}

.inputs__item {
  display: flex;
  width: 100%;
  height: fit-content;
  padding-bottom: 20px;
}

.inputs__img-block {
  width: 15%;
  border-radius: 4px 0 0 4px;
  background-color: #f0efe9;
  position: relative;
}

.inputs__icon {
  fill: #c4cbcd;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.inputs__input {
  padding: 12px;
  width: 85%;
  outline: none;
  font-size: 16px;
  font-family: 'Roboto';
  border-radius: 0 4px 4px 0;
}

.check {
  display: flex;
  justify-content: center;
}

.check__box {
  width: 20px;
  height: 20px;
  border-radius: 3px;
  background-color: #0ab19e;
  margin-right: 10px;
  position: relative;
}

.check__box:after {
  content: '';
  display: block;
  position: absolute;
  top: 45%;
  left: 50%;
  transform-origin: top left;
  transform: rotate(-45deg) translate(-50%, -50%);
  width: 12px;
  height: 6px;
  border-bottom: 3px solid #fff;
  border-left: 3px solid #fff;
}

.human-info__check {
  margin-bottom: 20px;
}

.radio {
  text-align: center;
}

.radio__question {
  margin-bottom: 10px;
}

.radio-btns {
  display: flex;
  justify-content: center;
}

.radio-btns__item {
  display: flex;
}

.radio-btns__item:not(:last-of-type) {
  margin-right: 35px;
}

.radio-btns__box {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #c4cbcd;
  position: relative;
  background-color: #fff;
  margin-right: 10px;
}

.radio-btns__box:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  background-color: #0ab19e;
  width: 8px;
  height: 8px;
  transform: translate(-50%, -50%);
}

.btn-auth {
  display: block;
  height: 100%;
  padding: 15px 25px;
  border-radius: 4px;
  border: 1px solid white;
  color: white;
}

.btn-auth:hover {
  border: 1px solid transparent;
  transition: background-color .3s, color .3s;
  background-color: white;
  color: #0ab19e;
  font-weight: 500;
}

.works__title {
  background-color: #faf8f0;
  background-image: url("images/");
}

.slider {
  display: flex;
  height: 100vh;
  flex-direction: column;
  flex-wrap: wrap;
}

.slider__descr {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #efeee9;
}

.slider__preview {
  height: 50%;
  width: 50%;
  background-color: #eeebdf;
}

.slider__controls {
  height: 50%;
  width: 50%;
}

.descr {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.descr__tech {
  text-transform: uppercase;
  padding: 20px 0;
  text-align: center;
  border-bottom: 1px solid #0ab19e;
  border-top: 1px solid #0ab19e;
  color: #0ab19e;
  width: 120%;
  margin-bottom: 35px;
}

.btn-show {
  display: block;
  width: 100%;
  height: 100%;
  padding: 15px 20px;
  display: flex;
  background-color: #0ab19e;
  color: #fff;
  border-radius: 5px;
}

.btn-show__icon {
  fill: #fff;
  margin-right: 10px;
}

.btn-show:hover {
  background-color: #009688;
}

.controls {
  display: flex;
}

.controls__item {
  position: relative;
  width: 50%;
  border-right: 1px solid rgba(255, 255, 255, 0.7);
  background-color: rgba(199, 198, 193, 0.6);
}

.controls__item:hover {
  background-color: rgba(188, 186, 177, 0.6);
}

.controls__link {
  display: block;
  width: 100%;
  height: 100%;
}

.controls__icon {
  fill: rgba(255, 255, 255, 0.7);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.controls__icon:hover {
  fill: #fff;
}

.controls__icon_next {
  transform: rotate(180deg);
}

.talk {
  background: url("../images/bg-works.jpg") center 150px no-repeat;
  background-size: cover;
  min-height: 1240px;
  position: relative;
}

.talk__top-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

.talk__bottom-area {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.talk__arrow {
  margin-bottom: 8%;
  transform: rotate(180deg);
}

.talk__form {
  margin-bottom: 8%;
}

.container {
  margin: 0 15%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.quotes {
  display: flex;
  flex-wrap: wrap;
  color: #455a64;
}

.quotes__item {
  width: 45%;
  display: flex;
  flex-direction: column;
}

.quotes__item:not(:nth-of-type(2n)) {
  margin-right: 5%;
}

.quotes__symb {
  font-family: 'FontAwesome';
  color: #d5d7d8;
  font-size: 36px;
  margin-bottom: 35px;
}

.quotes__text {
  margin-bottom: 25px;
}

.quotes__person {
  margin-top: auto;
}

.person {
  display: flex;
  color: #0ab19e;
}

.person__avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-right: 20px;
  overflow: hidden;
  position: relative;
}

.person__img {
  max-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form {
  width: 380px;
  border-radius: 4px;
  background-color: #fff;
}

.form__input-area {
  padding: 5%;
  border-radius: 4px 4px 0 0;
  position: relative;
  overflow: hidden;
  opacity: .85;
}

.form__input-blur {
  content: '';
  margin: -5%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  background: url("../images/bg-works.jpg") 50% 46% no-repeat;
}

.form__input,
.form__text-area {
  position: relative;
  padding: 20px;
  font-size: 16px;
  outline: none;
  border-radius: 5px;
  border: 0;
  width: 100%;
  color: #455a64;
}

.form__text-area {
  min-height: 140px;
  resize: none;
  font-family: "Roboto";
}

.form__input {
  margin-bottom: 15px;
}

@media (max-width: 1200px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  html {
    font-size: 15px;
  }
}

@media (max-width: 880px) {
  html {
    font-size: 14px;
  }

  .footer__row {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 0;
    padding-left: 0;
  }

  .footer__col {
    text-align: center;
  }

  .footer__col {
    width: 90%;
    justify-content: center;
  }

  .footer__col:not(:last-of-type) {
    margin-bottom: 20px;
  }

  .subtitle__text:after,
  .subtitle__text:before {
    width: 25px;
    height: 5px;
    transition: width .5s, height .5s;
  }

  .title__text:after,
  .title__text:before {
    width: 25px;
    height: 5px;
    transition: width .5s, height .5s;
  }

  .personal {
    flex-direction: column;
  }

  .personal__left-col {
    width: 100%;
    padding: 0 20% 10% 20%;
  }

  .personal__right-col {
    width: 100%;
    padding: 10% 20% 10% 20%;
  }

  .text-area h1:after,
  .text-area h1:before {
    width: 25px;
    height: 5px;
    transition: width .5s, height .5s;
  }

  .skills__title {
    text-align: center;
  }

  .skills__list {
    justify-content: center;
  }

  .circle:not(:last-of-type) {
    margin-right: 15px;
  }

  .blog {
    justify-content: center;
  }

  .blog__paper-list {
    display: none;
  }

  .blog__papers {
    padding: 0;
    width: 90%;
  }

  .main {
    background-image: url("../images/bg.jpg");
    background-size: cover;
  }

  .main__bg {
    display: none;
  }

  .slider {
    flex-wrap: nowrap;
  }

  .slider__descr {
    order: 2;
    flex: 1.5;
    width: 100%;
  }

  .slider__preview {
    order: 1;
    flex: 2;
    width: 100%;
  }

  .slider__controls {
    order: 3;
    flex: 1;
    width: 100%;
  }

  .container {
    margin: 0 5%;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .credits__author {
    display: block;
  }

  .credits__author:after {
    display: none;
  }

  .credits {
    text-align: center;
  }

  .menu-text__item:not(:last-of-type) {
    margin-right: 10px;
  }

  .hero_blog {
    min-height: 568px;
  }

  .intro__svg {
    display: none;
  }

  .subtitle__text {
    font-size: 28px;
  }

  .title__text {
    font-size: 48px;
  }

  .title__svg_works {
    transform: scale(1) translate(-50%, -50%);
  }

  .title__svg_talk {
    transform: scale(1) translate(-50%, -50%);
  }

  .personal__left-col {
    padding: 0;
  }

  .personal__right-col {
    padding: 10% 0;
  }

  .personal__skills {
    padding: 0 5%;
  }

  .personal__text-area {
    padding: 0 5%;
  }

  .text-area h1 {
    max-width: 75%;
  }

  .skills__list {
    flex-wrap: wrap;
  }

  .circle:nth-of-type(2n) {
    margin-right: 0;
  }

  .map {
    align-items: flex-start;
  }

  .map__contacts {
    width: 100%;
  }

  .main__auth {
    margin: auto;
  }

  .main__welcome {
    width: 90%;
    height: 65%;
    position: static;
    transform: translate(0, 0);
    margin: auto;
  }

  .main__credits {
    margin: auto;
    width: 75%;
  }

  .welcome__info {
    height: 100%;
    justify-content: center;
  }

  .welcome__avatar {
    max-width: 70%;
  }

  .login {
    width: 100%;
  }

  .slider__descr {
    flex: 2;
  }

  .descr__tech {
    width: 95%;
    margin-bottom: 15px;
    padding: 10px 0;
  }

  .quotes__item {
    width: 100%;
    margin-bottom: 15%;
  }

  .quotes__item:not(:nth-of-type(2n)) {
    margin-right: 0;
  }

  .quotes__symb {
    text-align: center;
  }

  .form {
    width: 100%;
  }
}